import { Component, OnInit,ViewChild ,ViewEncapsulation} from '@angular/core';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { TranslateService } from '@ngx-translate/core';
import { RemoteService } from '@app/remote.service';
import { CookieService } from 'ngx-cookie-service';
import { ActivatedRoute } from '@angular/router';
import { environment } from '@env/environment';
import { HttpClient } from '@angular/common/http';

import { MatSort } from '@angular/material/sort';
import { ApplicationService } from '@app/services/application.service';
import { Encrypt } from '@app/common/class/encrypt';
import swal from 'sweetalert';
@Component({
  selector: 'app-courseview',
  templateUrl: './courseview.component.html',
  styleUrls: ['./courseview.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class CourseviewComponent implements OnInit {
  @ViewChild("paginator") paginator: MatPaginator;

  @ViewChild(MatSort) sort: MatSort;
  resultsLength1: number;
  resultsLength2: number;
  resultsLength3: number;
  page: number = 10;
  tabtype: any;
  courseid: any;
  postParams: { 'courseid': any;  'pageSize': any; 'page': any;};
  postUrl: string;
  public fetchPageSize:number = 10;
  public fetchPage:number = 0;
  ifarabic: boolean;
  public arr = [];
  public arrayoffercourse = [];
  coursecategory: any;
  coursemst_pk = 3;
  levelarray: any;
  interarray: any;
  coursearray:any;
  disableSubmitButton: boolean = false;
  constructor(
    private translate: TranslateService,
    private remoteService: RemoteService,
   
    private cookieService: CookieService,
    private routeid: ActivatedRoute,
    private http: HttpClient,
    private appservice : ApplicationService,
    private security: Encrypt,
    
  ) { 

    this.onValidation = this.onValidation.bind(this);
  }
  languagelist = [{ "id": "1", "languageName": "English", "languagecode": "en", "CountryMst_Pk": "136", "dir": "ltr" },
  { "id": "2", "languageName": "Arabic", "languagecode": "ar", "CountryMst_Pk": "31", "dir": "rtl" }]
  dir = 'ltr';
  ngOnInit(): void {
    
    if (this.cookieService.get('languageCookieId') && this.cookieService.get('languageCookieId') != undefined && this.cookieService.get('languageCookieId') != null) {
      const toSelect = this.languagelist.find(c => c.id === this.cookieService.get('languageCookieId'));
      //this.patientCategory.get('patientCategory').setValue(toSelect);
      this.translate.setDefaultLang(toSelect.languagecode);
      this.dir = toSelect.dir;
    } else {
      const toSelect = this.languagelist.find(c => c.id == '1');
      //this.patientCategory.get('patientCategory').setValue(toSelect);
      this.translate.setDefaultLang(toSelect.languagecode);
      this.dir = toSelect.dir;
    }
    this.remoteService.getLanguageCookie().subscribe(data => {
      this.translate.setDefaultLang(this.cookieService.get('languageCode'));
      if (this.cookieService.get('languageCookieId') && this.cookieService.get('languageCookieId') != undefined && this.cookieService.get('languageCookieId') != null) {
        const toSelect = this.languagelist.find(c => c.id === this.cookieService.get('languageCookieId'));
        //this.patientCategory.get('patientCategory').setValue(toSelect);
        this.translate.setDefaultLang(toSelect.languagecode);
        this.dir = toSelect.dir;
        if (toSelect.languagecode == 'en') {
          this.ifarabic = false;
     
        }
        else {
          this.ifarabic = true;
        }
      } else {
        const toSelect = this.languagelist.find(c => c.id == '1');
        //this.patientCategory.get('patientCategory').setValue(toSelect);
        this.translate.setDefaultLang(toSelect.languagecode);
        this.dir = toSelect.dir;


      }
    });
    this.routeid.params.subscribe(params => {
      this.tabtype = params['type'];
      this.courseid = params['id'];
    });

    if(this.tabtype == 'course'){
      this.fetchCourceData(this.page);
      this.disableSubmitButton = true;
      this.appservice.getcoursecategory().subscribe(data => {
      this.coursecategory = data.data.data;
      });

      this.appservice.getreferancemst(this.coursemst_pk).subscribe(data => {
        this.levelarray = data.data.data;
      });

      this.appservice.getreferancemst(this.coursemst_pk).subscribe(data => {
        this.levelarray = data.data.data;
      });
      this.appservice.getappintermst().subscribe(data => {
       this.interarray = data.data.data;
       console.log(this.interarray,'inter');
      });

     
     
    }
    if(this.tabtype == 'staff'){
      this.disableSubmitButton = true;
      this.fetchStaffAcad(this.page);
      this.fetchStaffwrk(this.page);
      this.appservice.getcoursetmp().subscribe(data => {
        this.coursearray = data.data.data;
        console.log(this.coursearray[1] ,'lslsll')
      });
    }


  }
  syncPrimaryPaginator1(event: PageEvent) {
    this.paginator.pageIndex = event.pageIndex;
    this.paginator.pageSize = event.pageSize;
    this.page = event.pageSize;
    if(this.tabtype == 'course'){
      this.fetchCourceData(this.page);
    }
  }
  syncPrimaryPaginator2(event: PageEvent) {
    this.paginator.pageIndex = event.pageIndex;
    this.paginator.pageSize = event.pageSize;
    this.page = event.pageSize;
    if(this.tabtype == 'staff'){
      this.fetchStaffAcad(this.page);
    }
  }
  syncPrimaryPaginator3(event: PageEvent) {
    this.page = event.pageSize;
    if(this.tabtype == 'staff'){
      this.fetchStaffwrk(this.page);
    }
  }

  fetchCourceData(page) {
    this.disableSubmitButton = true;
    this.postUrl = 'center/app-center/fetch-favourite-result';
    this.postParams = {
      'courseid': this.courseid,
      'pageSize':this.page,
      'page':this.fetchPage,
    }
    this.appservice.AppSearhService(this.postParams, this.postUrl).subscribe(
      function (data) {
        this.courseunit = data['data'].data.favResult.data;
        this.appdt_status =  this.courseunit[0]['appdt_status'];
        this.courcename =  (this.ifarabic = true)?this.courseunit[0]['appoct_coursename_en']:this.courseunit[0]['appoct_coursename_ar'];
        this.appoct_courseduration = this.courseunit[0]['appoct_courseduration'];
        this.appoct_courselevel = this.courseunit[0]['appoct_courselevel'];
        this.appoct_foundationprog = (this.courseunit[0]['appoct_foundationprog'] = 1)?true:false;
        this.appoct_coursecategorymst_fk = (this.ifarabic = true)?this.courseunit[0]['ccm_catname_en']:this.courseunit[0]['ccm_catname_ar'];
        this.array = this.courseunit[0]['appoct_coursesubcategorymst_fk'].split(',');
       this.appoct_coursesubcategorymst_fk = this.courseunit[0]['appoct_coursesubcategorymst_fk'];
       this.appoct_coursetested = this.courseunit[0]['appoct_coursetested'];
       this.appoct_appintrecogtmp_fk = this.courseunit[0]['appoct_appintrecogtmp_fk'];
       this.intreg = this.courseunit[0]['appoct_appintrecogtmp_fk'].split(',');
       this.appoct_appdeccomment = (this.courseunit[0]['appoct_appdeccomment'])?this.courseunit[0]['appoct_appdeccomment']:'Nil';
       this.appoct_appdecon = this.courseunit[0]['appoct_appdecon'];
       this.appoct_appdecby = this.courseunit[0]['appoct_appdecby'];
       this.username = this.courseunit[0]['username'];
       this.appoct_status = this.courseunit[0]['appoct_status'];
       this.resultsLength1 = data['data'].data.favResult.totalcount;
    this.disableSubmitButton = false;

      }.bind(this)
    );
      }


      fetchStaffAcad(page) {
        this.disableSubmitButton = true;
        this.postUrl = 'center/app-center/fetch-favourite-staffacd';
        this.postParams = {
          'courseid': this.courseid,
          'pageSize':page,
          'page':this.fetchPage,
        }
        this.appservice.AppSearhService(this.postParams, this.postUrl).subscribe(
          function (data) {
          
            this.staffacd = data['data'].data.favResult.data;
            this.sir_idnumber =  this.staffacd[0]['sir_idnumber'];
            this.appdt_status =  this.staffacd[0]['appdt_status'];
            this.sir_name_en =  this.staffacd[0]['sir_name_en']
            this.sir_name_ar  =  this.staffacd[0]['sir_name_ar'];
            this.sir_emailid =   this.staffacd[0]['sir_emailid'];
            this.sir_dob =      this.staffacd[0]['sir_dob'];
            this.age             = this.staffacd[0]['age'];
            this.sir_gender =    this.staffacd[0]['sir_gender'];
            this.sir_nationality = this.staffacd[0]['sir_nationality'];
            this.sir_staffcv = this.staffacd[0]['sir_staffcv'];
            this.sir_opalstatemst_fk = this.staffacd[0]['sir_opalstatemst_fk'];
            this.sir_opalcitymst_fk = this.staffacd[0]['sir_opalcitymst_fk'];
            this.sir_addrline1 = this.staffacd[0]['sir_addrline1'];
            this.sir_addrline2 = this.staffacd[0]['sir_addrline2'];
            this.appsit_mainrole = this.staffacd[0]['appsit_mainrole'];
            this.appsit_jobtitle = this.staffacd[0]['appsit_jobtitle'];
            this.appsit_contracttype = this.staffacd[0]['appsit_contracttype'];
            this.appsit_appdeccomment = (this.staffacd[0]['appsit_appdeccomment'])?this.staffacd[0]['appsit_appdeccomment']:'Nil';
            this.appsit_appdecon = this.staffacd[0]['appsit_appdecon'];
            this.appsit_appdecby = this.staffacd[0]['appsit_appdecby'];
            this.appsit_status = this.staffacd[0]['appsit_status'];
            this.documenttype = this.staffacd[0]['sir_moheridoc'];
            this.username1 = this.staffacd[0]['username'];
            this.mcfd_filetype = this.staffacd[0]['mcfd_filetype'];
            this.coverImg = this.staffacd[0]['coverImages'];
            this.arrayoffercourse = this.staffacd[0]['appsit_appoffercoursetmp_fk'].split(',');
            this.resultsLength2 = data['data'].data.favResult.totalcount;
            this.disableSubmitButton = false;
          }.bind(this)
        );
    }
    fetchStaffwrk(page) {
      this.disableSubmitButton = true;
      this.postUrl = 'center/app-center/fetch-favourite-staffwrk';
      this.postParams = {
        'courseid': this.courseid,
        'pageSize':this.page,
        'page':this.fetchPage,
      }
      this.appservice.AppSearhService(this.postParams, this.postUrl).subscribe(
        function (data) {
           this.staffwrk = data['data'].data.favResult.data;
           this.resultsLength3 = data['data'].data.favResult.totalcount;
           this.disableSubmitButton = false;

        }.bind(this)
      );
  }

  onValidation(form ,resetForm){
    this.arr.push(this.security.decrypt(this.courseid));
    this.disableSubmitButton = true; 
    if(this.tabtype == 'course'){
    this.appservice.updateCourse(form.value,this.arr).subscribe(data => {
      this.disableSubmitButton = false; 
      swal({
        title: 'The Course View has been Validated and Submitted.',
        text: " ",
        icon: 'warning',
        buttons: [false, "Ok"],
        dangerMode: true,
        closeOnClickOutside: false
      }).then(() => {
        resetForm();
        
      });
      this.fetchCourceData(this.page);                       
    });

  }
    
  if(this.tabtype == 'staff'){
    this.appservice.updateStaff(form.value,this.arr).subscribe(data => {
      this.disableSubmitButton = false; 
      swal({
        title: 'The Staff View has been Validated and Submitted',
        text: " ",
        icon: 'warning',
        buttons: [false, "Ok"],
        dangerMode: true,
        closeOnClickOutside: false
      }).then(() => {
        resetForm();
        
      });
      this.fetchStaffAcad(this.page);
      this.fetchStaffwrk(this.page);
 
                            
     });
    }
}

staffCv(sir_staffcv){
  window.open(environment.baseUrl + 'web/cv/' + sir_staffcv, "_blank");
}
    
}





